module.exports = "<!--\r\n1. 文本\r\n2. 多行文本\r\n3. 复选框\r\n4. 单选按钮\r\n5. 选择框\r\n-->\r\n\r\n\r\n<template>\r\n    <div>\r\n        <div class=\"case-item\">\r\n            <h4>1. 文本</h4>\r\n            <input v-model=\"input\" placeholder=\"edit me\">\r\n            <p>Message is: {{ input }}</p>\r\n        </div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>2. 多行文本</h4>\r\n            <span>Multiline message is:</span>\r\n            <p style=\"white-space: pre-line;\">{{ textarea }}</p>\r\n            <br>\r\n            <textarea v-model=\"textarea\" placeholder=\"add multiple lines\"></textarea>\r\n        </div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>3. 复选框</h4>\r\n            <div>\r\n                <input type=\"checkbox\" id=\"jack\" value=\"Jack\" v-model=\"checkedNames\">\r\n                <label for=\"jack\">Jack</label>\r\n                <input type=\"checkbox\" id=\"john\" value=\"John\" v-model=\"checkedNames\">\r\n                <label for=\"john\">John</label>\r\n                <input type=\"checkbox\" id=\"mike\" value=\"Mike\" v-model=\"checkedNames\">\r\n                <label for=\"mike\">Mike</label>\r\n                <br>\r\n                <span>Checked names: {{ checkedNames }}</span>\r\n            </div>\r\n        </div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>4. 单选按钮</h4>\r\n            <div>\r\n                <input type=\"radio\" id=\"one\" value=\"One\" v-model=\"picked\">\r\n                <label for=\"one\">One</label>\r\n                <br>\r\n                <input type=\"radio\" id=\"two\" value=\"Two\" v-model=\"picked\">\r\n                <label for=\"two\">Two</label>\r\n                <br>\r\n                <span>Picked: {{ picked }}</span>\r\n            </div>\r\n        </div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>5. 选择框</h4>\r\n            <select v-model=\"selected\">\r\n                <option disabled value=\"\">请选择</option>\r\n                <option>A</option>\r\n                <option>B</option>\r\n                <option>C</option>\r\n            </select>\r\n            <span>Selected: {{ selected }}</span>\r\n        </div>\r\n\r\n\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Watch} from 'vue-property-decorator'\r\n\r\n    @Component\r\n    export default class BasicUsage extends Vue {\r\n        input = ''\r\n        textarea = ''\r\n        checkedNames = []\r\n        picked = ''\r\n        selected = ''\r\n    }\r\n</script>\r\n\r\n\r\n\r\n";